import { Component, Input } from '@angular/core';

@Component({
  selector: 'zippy',
  templateUrl: './zippy.component.html',
  styleUrls: ['./zippy.component.css']
})
export class ZippyComponent {

  constructor() { }

  unfolded = false;
  @Input('title')
  title = "test title";

  onClick() {
    this.unfolded  = !this.unfolded;
  }
}
